.body {
  margin-left: 280px;
}

.wrapper {
  text-align: center;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;
  width: 450px;
  height: 550px;
  border: solid 1px transparent;
  border-radius: 5%;
  background-color: antiquewhite;
}

.wrapper .imgBox {
  display: flex;
  width: 120px;
  height: 120px;
  margin-top: 70px;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  border-radius: 50%;
  margin-bottom: 80px;
}

.imgIcon {
  width: 50px;
  height: 50px;
  object-fit: cover;
}

.inputBox {
  width: 450px;
  height: 150px;
  margin-bottom: 30px;
}

.inputBox .idBox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.idBox .idIcon {
  width: 30px;
  height: 30px;
  object-fit: cover;
  margin-right: 10px;
}

.inputtext {
  text-align: left;
  width: 250px;
  height: 40px;
  border: none;
  border-bottom: solid 2px coral;
  background: none;
  margin-bottom: 10px;
  color: #868b94;
}

.inputtext:focus {
  outline: none;
}

.inputBox .psBox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.psBox .psIcon {
  width: 30px;
  height: 30px;
  object-fit: cover;
  margin-right: 10px;
}

.bBox {
  width: 450px;
  height: 50px;
}

.button {
  width: 250px;
  height: 50px;
  border: solid 1px transparent;
  border-radius: 8px;
  background-color: #ffab91;
}

.logSpan {
  font-size: 25px;
}

.signUpBox {
  width: 450px;
  height: 50px;
  margin-top: 5px;
}

.signButton {
  border: solid 1px transparent;
  background-color: transparent;
}

.signSpan {
  font-size: 20px;
  color: #868b94;
  font-size: 13px;
  cursor: pointer;
}
